@charset "utf-8";

@import url('https://spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css'); 
@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css); 

@font-face {
    font-family: 'noto_sans_kr';
    src: url('./fonts/notosans/NotoSansKR-Medium.otf');
}

:root {
    --default_color: #6D39FF;
    --Orange_color: #EA533C;
}

* { box-sizing: border-box; }

*, html, body {
	font-family: "noto_sans_kr", sans-serif; 
	-webkit-text-size-adjust:none;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

html {height:100%; overflow-y: scroll; -webkit-overflow-scrolling : touch; overflow-x: hidden; }
body {
    margin:0;
    padding:0;
    font-size:13px;
    font-weight: 400;
    height:100%;
    /* color:#000; */
    /* min-width: 1920px; */
}

html, body { background-color: #fff; width: 100%; height:100%;  }
/* html, body { background-color: #fff; width: 100%; height:100%;  } */

html, h1, h2, h3, h4, h5, h6, form, fieldset, img {margin:0;padding:0;border:0}
h1, h2, h3, h4, h5, h6 {font-weight:700; font-size:15px;}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {display:block}
#hd ul, nav ul, #ft ul {margin:0;padding:0;list-style:none}
legend {position:absolute;margin:0;padding:0;font-size:0;line-height:0;text-indent:-9999em;overflow:hidden}
label, input, button, select, img {vertical-align:middle}
input, button {margin:0;padding:0;font-size:1em; outline: none;}
button {cursor:pointer; outline: none; border: none; background: #fff;}
textarea, select {font-size:1em; outline: none;}
select {margin:0}
p {margin:0;padding:0;word-break:keep-all;}
hr {display:none}
pre {overflow-x:scroll;font-size:1.1em}

a:link, a:visited {font-size:13px;font-weight: 500;color:#000;text-decoration:none}
a:hover, a:focus, a:active {color:#000;text-decoration:none}
ul {padding:0; margin:0; list-style:none;}
li {padding:0; margin:0; list-style:none;}

.fontbrr { font-family: 'br_rregular' !important; } 
.fl { float: left ; }
.fr { float: right; }
.alignc { text-align: center !important; }
.alignl { text-align: left !important; }
.fontb { font-weight: 700; }
.height100 { line-height: 100px; }
.height200 { line-height: 200px; }

.color666 { color: #666; }
.color333 { color: #333; }
.fonts18 { font-size: 18px; }

