.vertical_div { display: table; width: 100%; height: 100%; vertical-align: middle; }
.vertical_div > div { display: table-cell; width: 100%; height: 100%; vertical-align: middle; }

.borderb2px { padding-bottom: 30px; border-bottom: 2px solid #ddd; }

.pagetitle { font-size: 20px; font-weight: 700; }
.pagetitle { width: 1100px; margin: 30px auto 0; text-align: left;  }
.pagetitle > span { 
	color: #999; font-size: 13px; font-weight: 400; position: relative; 
	margin-left: 24px; 
}
.pagetitle > span::before{
    content:'';
    display:inline-block;
    width:1px;
    height:13px;
    position: absolute;
    left: -12px;
    top: 3px; 
    background: rgba(0, 0, 0, 0.12);    	
}

.pagetitleImg { width: 1100px; margin: 24px auto 0; text-align: left; }
.pagetitleImg > img {width: 100%;}

.pagesearchtitle { font-size: 24px; font-weight: 400; }
.pagesearchtitle { width: 1100px; margin: 30px auto 0; text-align: left;  }
.pagesearchtitle > b  { color: #0EA55B; }

.dltitle { line-height: 18px; top: calc(50% - 16px);}
.main_body {
    display: flex;
    align-items: center;
    justify-content: center;
}
.input_box {
    
}
.text_input_default {
    width: 100%;
    height: 100%;
    font-size: 14px;
    font-weight: 500;
    /* line-height: 20.27px; */
    border: 1px solid #ddd;
    position: relative;
    padding: 16px 12px;
    border-radius: 4px;
}
.text_input_default:focus {
    outline:none;
}
.text_input_default::placeholder {
    color: #A6A19E;
}
.input_error {
    color: #ff0000;
    font-size: 10px;
    font-weight: 400;
    line-height: 14.48px;
    margin-top: 10px;
}
.button01_defalut {
    background-color: var(--default_color);
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    border-radius: 4px;
}
.button01_defalut_border {
    background-color: #fff;
    color: var(--default_color);
    font-size: 16px;
    font-weight: 700;
    border-radius: 4px;
    border: 1px solid var(--default_color);
}
.button02_defalut {
    background-color: var(--default_color);
    color: #fff;
    font-size: 16px;
    font-weight: 700;
    border-radius: 32px;
}
.button02_defalut_border {
    background-color: #fff;
    color: var(--default_color);
    font-size: 16px;
    font-weight: 700;
    border-radius: 32px;
    border: 1px solid var(--default_color);
}
.loading {
    display: none;
    align-items: center;
    justify-content: center;
    position: fixed;
    width: 100%;
    height: 100%;
    z-index: 100000;
    top: 0;
    left: 0;
    background-color: rgba(255, 255, 255, .7);
    /* background-color: #EFE7DE; */
}
.loading.show {
    display: flex;
}
.popup {
    display: none;
    align-items: center;
    justify-content: center;
    position: fixed;
    z-index: 9999;
    width: 100%;
    height: 100%;
    background-color: rgb(000, 000, 000, 0.7);
    top: 0;
    left: 0;
    padding: 0px 10px;
}
.popup.show {
    display: flex;
    animation: popup-show 0.3s;
}
.header {
    height: 56px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 12px;
    line-height: 23.17px;
}
.center_text {
    font-size: 16px;
    font-weight: 700;
}
.cancel_text {
    font-size: 14px;
    font-weight: 700;
}
.footer_button {
    width: 100%;
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 700;
    color: #fff;
}
.flex_container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.input_timer {
    display: flex;
    align-items: center;
    position: absolute;
    top: 15px;
    right: 15px;
    width: 25px;
    height: 14px;
}
.input_timer > p {
    font-size: 10px;
    font-weight: 400;
    line-height: 14.48px;
    color: #ff0000;
}
.input_label {
    font-weight: 700;
    font-size: 12px;
    color: #666;
}
.input_label > span {
    color: red;
}



@keyframes popup-show {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
@keyframes popup-container-show {
    from {
        opacity: 0;
        margin-top: -50px;
    }
    to {
        opacity: 1;
        margin-top: 0;
    }
}

