:root {
    --default_color: #6D39FF;
    --Orange_color: #EA533C;
}

@font-face {
    font-family: 'NotoSansR';
    src: url('./fonts/avenir/NotoSansKR-Regular.ttf');
}

@font-face {
    font-family: 'NotoSansM';
    src: url('./fonts/avenir/NotoSansKR-Medium.ttf');
}

@font-face {
    font-family: 'NotoSansB';
    src: url('./fonts/avenir/NotoSansKR-Bold.ttf');
}

@font-face {
    font-family: 'NotoSansBB';
    src: url('./fonts/avenir/NotoSansKR-Black.ttf');
}

.login_content {
    
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.login_title {
    padding-top: 129.5px;
    font-weight: 700;
    font-size: 24px;
    margin: 0 auto;
    margin-bottom: 20px;
    width: 310px;
}
.login_bottom {
    margin-top: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.login_bottom > p {
    font-size: 14px;
    font-weight: 400;
}
.center_bar {
    background-color: #ccc;
    width: 1px;
    height: 16px;
    margin: 0px 10px;
}
.login_register {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 40px;
}
.login_register > p {
    font-family: 'Roboto Medium';
    font-size: 14px;
    font-weight: 400;
    color: #666;
}
.register_type_box {
    width: 208px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.popup_default01 {
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: 10px;
    padding: 32px;
}
.popup_text_box {
    font-family: 'Roboto Medium';
    font-size: 14px;
    font-weight: 400;
    padding-bottom: 24px;
    white-space: pre-wrap;
    text-align: center;
}
.popup_btn_box {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.popup_btn_box > button {
    width: 138px;
    height: 48px;
    border-radius: 8px;
    color: #fff;
    font-weight: 700;
    font-size: 14px;
    line-height: 20.27px;
}
.popup_btn_box > .button_cancel {
    background-color: #ccc;
    margin-right: 10px;
}
.popup_btn_box > .button_success {
    background-color: #101010;
}
.register_box {
    padding-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.register_box02 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.register_title {
    font-size: 24px;
    font-weight: 700;
    text-align: center;
}
.register_btn_box {
    padding-top: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    gap: 10px;
}
.terms_all_box {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    margin-top: 20px;
    padding: 10px;
    border-bottom: 1px solid #eee;
    width: 310px;
}
.terms_all_text {
    margin-left: 5px;
}
.terms_all_text > .title {
    font-size: 16px;
    font-weight: 700;
    line-height: 23.17px;
    padding-bottom: 5px;
}
.terms_all_text > .content {
    font-size: 12px;
    font-weight: 400;
    line-height: 17.38px;
    color: #666;
}
.terms_content_box {
    margin-top: 10px;
    padding: 10px;
}
.terms_content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 290px;
}
.terms_sub_content01 {
    width: 64px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.terms_sub_content02 {
    width: 241px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.chk_default {
    width: 35px;
    height: 22px;
    font-size: 10px;
    font-weight: 700;
    line-height: 12px;
    text-align: center;
    padding: 4px 8px 4px 8px;
    border-radius: 100px;
    border: 1px solid #ccc;
    color: #ccc;
    margin-left: 5px;
}
.chk_true {
    border: 1px solid #101010;
    color: #101010;
}
.terms_content_title {
    font-size: 14px;
    font-weight: 400;
    line-height: 15px;
    margin-left: 4px;
    border-bottom: 1px solid #333;
    cursor: pointer;
}
.terms_content > .right {
    line-height: 22px;
}
.terms_popup_box {
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 40px;
}
.terms_popup_box > p {
    width: 310px;
    height: 144px;
    font-size: 12px;
    font-weight: 400;
    color: #666;
}
.register_box_title {
    padding-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.register_box_title > p {
    width: 310px;
    font-size: 24px;
    font-weight: 700;
}
.register_email {
    font-size: 16px;
    font-weight: 700;
    line-height: 23.17px;
}
.register_email > p {
    width: 310px;
    text-align: left;
}
.chk_email_num {
    width: 310px;
    display: flex;
    align-items: center;
}
.register_email02 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.register_email02 > p {
    text-align: center;
    font-size: 16px;
    font-weight: 700;
}
.register_avatar {
    width: 72px;
    height: 72px;
    position: relative;
    cursor: pointer;
}
.register_avatar > img {
    width: 72px;
    height: 72px;
    border-radius: 50%;
    object-fit: cover;
}
.register_avatar_camera {
    position: absolute;
    bottom: 0px;
    right: -12px;
}
.register_avatar_col {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.register_info_title {
    font-weight: 400;
    font-size: 14px;
    color: #666;
}
.register_info_email {
    margin-top: 5px;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 14px;
}
.suc_container {
    margin-top: 205.5px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.suc_title {
    font-family: 'NotoSansBB';
    font-size: 32px;
    line-height: 46.34px;
    color: var(--default_color);
    margin-bottom: 40px;
}
.suc_name {
    font-family: 'NotoSansB';
    font-size: 24px;
    margin-bottom: 10px;
}
.suc_title02 {
    font-weight: 400;
    font-size: 14px;
    color: #666;
    margin-bottom: 40px;
}